import React, { Component } from "react";
import { isMobile } from "./isMobile";
// creating waiting function to slide photos
export function wait(t) {
  return new Promise(e => setTimeout(e, t));
}
// 2 Variable to controll accsess to sliding to avoid error
let isSlidding = true;
let isClickable = true;
class Slider extends Component {
  componentDidMount() {
    // show the dots indicator always on mobile
    if (isMobile()) {
      const el = document.getElementById("dots");
      el.style.opacity = 1;
    }
    // inject one div for every image url
    for (let i = 0; i < this.props.data.length; i++) {
      const dots = document.getElementById("dots");
      const element = document.getElementById("imageContainer");
      element.innerHTML += `
            <div class="sliderElement" id="slide${i}" style="background-image: url(${this.props.data[i]})"></div>
            `;
      // inject one dot indicator for every image url
      dots.innerHTML += `
            <svg xmlns="http://www.w3.org/2000/svg" height="15" viewBox="0 0 24 24" width="15">
            <circle cx="12" cy="12" r="8"/>
            </svg>
            `;
    }
    // stop auto image sliding when mouse enters
    document
      .getElementById("sliderContainer")
      .addEventListener("mouseenter", _ => (isSlidding = false));
    // start auto image sliding when mouse leaves
    document
      .getElementById("sliderContainer")
      .addEventListener("mouseleave", _ => (isSlidding = true));

    let index = 0; // index variable refer to image orders
    const elements = document.querySelectorAll(".sliderElement");
    const dots = document.querySelectorAll("#dots svg");
    const activeDotColor = "white";
    const normalDotColor = "green";
    // color the first dot
    dots[0].style.fill = activeDotColor;
    setInterval(async () => {
      if (isSlidding) {
        // when slider reches the last image reset every image and dot style to defualt in order to start again
        if (index >= elements.length - 1) {
          for (let i = 0; i < elements.length; i++) {
            // trun off the css transition animation to make the reset faster
            elements[i].style.transition = "none";
            // reset images style back
            elements[i].style.display = "inline-block";
            elements[i].style.width = "100%";
            // reset dots color to normal
            dots[i].style.fill = normalDotColor;
            // color the first dot
            dots[0].style.fill = activeDotColor;
          }
          // reset the index to 0
          index = 0;
        } else {
          // turn on css transition animation
          elements[index].style.transition = "all 1s ease-in-out";
          // hide the image by setting the width to zero, you can try /transform = scale(0)/ or /opacity = 0/
          elements[index].style.width = 0;

          dots[index + 1].style.fill = activeDotColor;
          dots[index].style.fill = normalDotColor;
          await wait(1000);
          elements[index].style.display = "none";
          index++;
        }
      }
    }, 2500);
    document.getElementById("rightArrow").addEventListener("click", async _ => {
      if (isClickable) {
        // make arrow unresponsive to clicks
        isClickable = false;
        // when slider reches the last image reset every image and dot style to defualt in order to start again
        if (index >= elements.length - 1) {
          for (let i = 0; i < elements.length; i++) {
            elements[i].style.transition = "none";
            elements[i].style.display = "inline-block";
            elements[i].style.width = "100%";
            dots[i].style.fill = normalDotColor;
          }
          // reset the index to 0
          index = 0;
          // color the first dot
          dots[index].style.fill = activeDotColor;
        } else {
        // reset every image style to defualt in case you pressed on the arrow after pressing a dot
          for (let i = index; i < elements.length; i++) {
            elements[i].style.display = "inline-block";
            elements[i].style.width = "100%";
          }
          // turn css animation on
          elements[index].style.transition = "all 1s ease-in-out";
          elements[index].style.width = 0;
          // color the active dot
          dots[index + 1].style.fill = activeDotColor;
          // color the unactive dot
          dots[index].style.fill = normalDotColor;
          // wait untill the animation is done
          await wait(1000);
          // unmout the element to prevent white space on the side of slider
          elements[index].style.display = "none";
          index++;
        }
        // wait after clicking to prevent wierd behaviors
        await wait(500);
        isClickable = true;
      }
    })
    document.getElementById("leftArrow").addEventListener("click", async _ => {
      if (isClickable) {
        isClickable = false;
        if (index - 1 < 0) {
          for (let i = 0; i < elements.length; i++) {
            elements[i].style.transition = "none";
            elements[i].style.display = "none";
            elements[i].style.width = 0;
            dots[i].style.fill = normalDotColor;
          }
          index = elements.length - 1;
          elements[index].style.display = "inline-block";
          await wait(10);
          elements[index].style.width = "100%";
          dots[index].style.fill = activeDotColor;
        } else {
          elements[index - 1 < 0 ? 0 : index - 1].style.transition =
            "all 1s ease-in-out";
          elements[index - 1 < 0 ? 0 : index - 1].style.display =
            "inline-block";
          await wait(10);
          elements[index - 1 < 0 ? 0 : index - 1].style.width = "100%";
          dots[index].style.fill = normalDotColor;
          dots[index - 1].style.fill = activeDotColor;
          index--;
        }
        await wait(500);
        isClickable = true;
      }
    });
    for (let i = 0; i < dots.length; i++) {
      dots[i].addEventListener("click", async _ => {
        for (let dex = 0; dex < elements.length; dex++) {
          elements[dex].style.width = 0;
          elements[dex].style.display = "none";
          dots[dex].style.fill = normalDotColor;
        }
        elements[i].style.display = "inline-block";
        await wait(10);
        elements[i].style.width = "100%";
        dots[i].style.fill = activeDotColor;
        index = i;
        for (let ii = i; ii < dots.length; ii++) {
          elements[ii].style.width = "100%";
        }
      });
    }

    // making slider width 100% für Phons

    if (isMobile()) {
      const classelements = document.getElementsByClassName("sliderElement");
      for (let index = 0; index < classelements.length; index++) {
        classelements[index].style.backgroundSize = "98%";
        classelements[index].style.marginTop = 0;
        classelements[index].style.height = "200px";
      }
    }
    // swipe touch
    let xDown = null;
    document
      .getElementById("sliderContainer")
      .addEventListener("touchstart", handleTouchStart, false);
    document
      .getElementById("sliderContainer")
      .addEventListener("touchmove", handleTouchMove, false);
    function handleTouchStart(evt) {
      xDown = evt.touches[0].clientX;
    }
    async function handleTouchMove(evt) {
      if (!xDown) {
        return;
      }

      const xUp = evt.touches[0].clientX;
      const xDiff = xDown - xUp;

      if (xDiff > 0) {
        /* left swipe */
        if (isClickable) {
          isClickable = false;
          isSlidding = false;
          if (index >= elements.length - 1) {
            for (let i = 0; i < elements.length; i++) {
              elements[i].style.transition = "none";
              elements[i].style.display = "inline-block";
              elements[i].style.width = "100%";
              dots[i].style.fill = normalDotColor;
            }
            index = 0;
            dots[index].style.fill = activeDotColor;
          } else {
            for (let i = index; i < elements.length; i++) {
              elements[i].style.display = "inline-block";
              elements[i].style.width = "100%";
            }
            elements[index].style.transition = "all 1s ease-in-out";
            elements[index].style.width = 0;
            dots[index].style.fill = normalDotColor;
            dots[index + 1].style.fill = activeDotColor;
            await wait(1000);
            elements[index].style.display = "none";
            index++;
          }
          await wait(500);
          isClickable = true;
          await wait(1000);
          isSlidding = true;
        }
      } else {
        /* right swipe */
        if (isClickable) {
          isClickable = false;
          isSlidding = false;
          if (index - 1 < 0) {
            for (let i = 0; i < elements.length; i++) {
              elements[i].style.transition = "none";
              elements[i].style.display = "none";
              elements[i].style.width = 0;
              dots[i].style.fill = normalDotColor;
            }
            index = elements.length - 1;
            elements[index].style.display = "inline-block";
            await wait(10);
            elements[index].style.width = "100%";
            dots[index].style.fill = activeDotColor;
          } else {
            elements[index - 1 < 0 ? 0 : index - 1].style.transition =
              "all 1s ease-in-out";
            elements[index - 1 < 0 ? 0 : index - 1].style.display =
              "inline-block";
            await wait(10);
            elements[index - 1 < 0 ? 0 : index - 1].style.width = "100%";
            dots[index].style.fill = normalDotColor;
            dots[index - 1].style.fill = activeDotColor;
            index--;
          }
          await wait(500);
          isClickable = true;
          await wait(1000);
          isSlidding = true;
        }
      }
      /* reset values */
      xDown = null;
    }
  }

  render() {
    return (
      <div id="sliderContainer">
        <div id="imageContainer"></div>
        <svg
          className="arrow"
          id="leftArrow"
          xmlns="http://www.w3.org/2000/svg"
          height="50"
          viewBox="0 0 24 24"
          width="50"
        >
          <path d="M15.41 16.59L10.83 12l4.58-4.59L14 6l-6 6 6 6 1.41-1.41z" />
        </svg>
        <svg
          className="arrow"
          id="rightArrow"
          xmlns="http://www.w3.org/2000/svg"
          height="50"
          viewBox="0 0 24 24"
          width="50"
        >
          <path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z" />
        </svg>
        <div id="dots"></div>
      </div>
    );
  }
}

export default Slider;
