import React, { Component } from "react";
import { wait } from "../slider";

class ProductPhotoViewer extends Component {
  componentDidMount() {
    document.getElementById("photoVieweBox").style.backgroundImage = `url(${this.props.imgs[0]})`
    const el = document.getElementById("ProductPhotoViewerIconsContainer");
    for (let index = 0; index < this.props.imgs.length; index++) {
      const newDiv = document.createElement("div");
      newDiv.style.backgroundImage = `url(${this.props.imgs[index]})`;
      newDiv.classList.add("ProductPhotoViewerImg");
      
      newDiv.addEventListener("click", _=> {
        document.getElementById("photoVieweBox").style.backgroundImage = `url(${this.props.imgs[index]})`
      })
      el.append(newDiv);
    }
  }
  scrollToRight = async _ => {
    console.log("right clicked");
    const el = document.getElementById("ProductPhotoViewerIconsContainer");
    const getElWidth = Number(
      getComputedStyle(el)
        .getPropertyValue("width")
        .split("px")[0]
    );
    console.log(getElWidth);
    // scroll position start from the left
    el.scrollTo({
      // the scroll position from the left + the element container width
      left: el.scrollLeft + getElWidth,
      behavior: "smooth"
    });
  
  };
  // left arrow onClick function
  scrollToLeft = async _ => {
    const el = document.getElementById("ProductPhotoViewerIconsContainer");
    const getElWidth = Number(
      getComputedStyle(el)
        .getPropertyValue("width")
        .split("px")[0]
    );
    el.scrollTo({
      // prevent scroll bellow 0
      left: el.scrollLeft <= 0 ? 0 : el.scrollLeft - getElWidth,
      behavior: "smooth"
    });
  };
  render() {
    return (
      <div id="ProductPhotoViewerContainer">
       

        <div id="photoVieweBox">
 {/* hier are the Photo view */}
        </div>
        <div id="ViewerArrowOutSideDiv">
          <svg
            onClick={this.scrollToLeft}
            className="ViewerArrow productArrow productRightArrow"
            id="ViewerLeftArrow"
            xmlns="http://www.w3.org/2000/svg"
            height="30"
            viewBox="0 0 24 24"
            width="30"
          >
            <path d="M15.41 16.59L10.83 12l4.58-4.59L14 6l-6 6 6 6 1.41-1.41z" />
          </svg>
          <div id="ProductPhotoViewerIconsContainer">
            {/* hier are the productphoto */}
          </div>

          <svg
            onClick={this.scrollToRight}
            className="ViewerArrow productArrow productRightArrow"
            id="ViewerRightArrow"
            xmlns="http://www.w3.org/2000/svg"
            height="30"
            viewBox="0 0 24 24"
            width="30"
          >
            <path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z" />
          </svg>
        </div>
      </div>
    );
  }
}

export default ProductPhotoViewer;
