import React from 'react';
export default function Rating(props) {

    const starBG = "LightGray";
    const starActive = "gold";
    const starWidth = 18;
    const starHeight = 18;
    return (

        <div id="productRating">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={starWidth}
          height={starHeight}
          viewBox="0 0 24 24"
        >
          <linearGradient
            id="grad"
            x1="0"
            x2="0"
            y1="0"
            y2="100%"
            gradientTransform="rotate(-90)"
          >
            <stop offset="50%" stopColor={starActive} />
            <stop offset="50%" stopColor={starBG} />
          </linearGradient>
          <path
            d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
            fill={
               props.rate > 0 && props.rate !== 0.5
                ? starActive
                : props.rate === 0.5
                ? "url(#grad)"
                : starBG
            }
          />
        </svg>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={starWidth}
          height={starHeight}
          viewBox="0 0 24 24"
        >
          <linearGradient
            id="grad"
            x1="0"
            x2="0"
            y1="0"
            y2="100%"
            gradientTransform="rotate(-90)"
          >
            <stop offset="50%" stopColor={starActive} />
            <stop offset="50%" stopColor={starBG} />
          </linearGradient>
          <path
            d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
            fill={
               props.rate > 1 &&  props.rate !== 1.5
                ? starActive
                :  props.rate === 1.5
                ? "url(#grad)"
                : starBG
            }
          />
        </svg>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={starWidth}
          height={starHeight}
          viewBox="0 0 24 24"
        >
          <linearGradient
            id="grad"
            x1="0"
            x2="0"
            y1="0"
            y2="100%"
            gradientTransform="rotate(-90)"
          >
            <stop offset="50%" stopColor={starActive} />
            <stop offset="50%" stopColor={starBG} />
          </linearGradient>
          <path
            d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
            fill={
               props.rate > 2 &&  props.rate !== 2.5
                ? starActive
                :  props.rate === 2.5
                ? "url(#grad)"
                : starBG
            }
          />
        </svg>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={starWidth}
          height={starHeight}
          viewBox="0 0 24 24"
        >
          <linearGradient
            id="grad"
            x1="0"
            x2="0"
            y1="0"
            y2="100%"
            gradientTransform="rotate(-90)"
          >
            <stop offset="50%" stopColor={starActive} />
            <stop offset="50%" stopColor={starBG} />
          </linearGradient>
          <path
            d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
            fill={
               props.rate > 3 &&  props.rate !== 3.5
                ? starActive
                :  props.rate === 3.5
                ? "url(#grad)"
                : starBG
            }
          />
        </svg>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width={starWidth}
          height={starHeight}
          viewBox="0 0 24 24"
        >
          <linearGradient
            id="grad"
            x1="0"
            x2="0"
            y1="0"
            y2="100%"
            gradientTransform="rotate(-90)"
          >
            <stop offset="50%" stopColor={starActive} />
            <stop offset="50%" stopColor={starBG} />
          </linearGradient>
          <path
            d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
            fill={
               props.rate > 4 &&  props.rate !== 4.5
                ? starActive
                :  props.rate === 4.5
                ? "url(#grad)"
                : starBG
            }
          />
        </svg>
        <span id="ratespan">{props.rate}</span>
      </div>
    )
}