import React, { Component } from "react";
import SideNavBar from "./sideNavBar";
import { wait } from "./slider";
import {
  Link
} from "react-router-dom";

class NavBar extends Component {
  //hiding the View of basket and log in wenn searchbar is foxed in order to save view space on Phone
  componentDidMount() {
    const el = document.getElementById("searchbar");
    document.getElementById("searchIcon").addEventListener("click", async _ => {
      const getElStyle = window
        .getComputedStyle(el)
        .getPropertyValue("display");
      if (getElStyle === "none") {
        el.style.display = "block";
        el.focus()
        await wait(10);
        el.style.opacity = 1;
        if( document.getElementsByClassName("adsTextElement")[0]){
          
          document.getElementsByClassName("adsTextElement")[0].style.display =
            "none";
        }
      } else {
        el.style.opacity = 0;
        await wait(350);
        el.removeAttribute("style");
        document
          .getElementsByClassName("adsTextElement")[0]
          .removeAttribute("style");
      }
    });

    el.addEventListener("blur", async _ => {
      if (window.innerWidth <= 400){
        el.style.opacity = 0;
        await wait(350);
        el.removeAttribute("style");
        document
          .getElementsByClassName("adsTextElement")[0]
          .removeAttribute("style");
      }
    });
  }
  render() {
    return (
      <nav className="navbar">
        <SideNavBar />
        <Link to="/" className="CompanyTitleLink">
        <h4 className="CompanyTitle">Bio Class</h4>
        </Link>
      
        <input placeholder="Search" id="searchbar"></input>
        {/* <div id="searchIcon"></div> */}
        <svg
          id="searchIcon"
          xmlns="http://www.w3.org/2000/svg"
          height="30"
          viewBox="0 0 24 24"
          width="30"
        >
          <path d="M0 0h24v24H0V0z" fill="none" />
          <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z" />
        </svg>
        {/* <Link to="/about">About</Link> */}
  
        <svg
          id="basket"
          className="login"
          xmlns="http://www.w3.org/2000/svg"
          height="33"
          viewBox="0 0 24 24"
          width="33"
        >
          <path d="M17.21 9l-4.38-6.56c-.19-.28-.51-.42-.83-.42-.32 0-.64.14-.83.43L6.79 9H2c-.55 0-1 .45-1 1 0 .09.01.18.04.27l2.54 9.27c.23.84 1 1.46 1.92 1.46h13c.92 0 1.69-.62 1.93-1.46l2.54-9.27L23 10c0-.55-.45-1-1-1h-4.79zM9 9l3-4.4L15 9H9zm3 8c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z" />
        </svg>
        {/* <div className="login">
          <h6 id="login">Login</h6>
        </div> */}
      </nav>
    );
  }
}



export default NavBar;
