import React, { Component } from "react";
import { ProductData } from "./ProductData";
import Rating from "./Rating";
import ProductPhotoViewer from "./ProductPhotoViewer";
import ProductWindows from "../ProductWindows";

// style for price old/new
const style1 = {
  color: "red",
  marginLeft: "10px"
};
const style2 = {
  color: "black",
  marginLeft: "0"
};
class ProductPage extends Component {
  state = {
    isLoading: true,
    data: ""
  };
  componentDidMount() {
    this.getProductData();
  }
  getProductData = _ => {
    this.setState({
      data: ProductData.filter(e => e.id === Number(this.props.id))[0],
      isLoading: false
    });
  };

  render() {
    if (this.state.isLoading) {
      return <h4>Loading...</h4>;
    } else {
      return (
        <div className="ProductPage">
          <div id="productPhotosAndInfoDiv">
            <ProductPhotoViewer imgs={this.state.data.pic} />
            <div id="ProductPageInfoSide">
              <h1>{this.state.data.name} </h1>
              <Rating rate={this.state.data.rating} />
              <h6 id="shortDiscription">{this.state.data.shortDiscription}</h6>

              <div className="ProductPricing" id="poductPagPricing">
                <h5 id="productOldPrice">
                  {this.state.data.oldPrice}
                  {this.state.data.oldPrice ? "€" : ""}
                </h5>
                <h5
                  id="productPrice"
                  style={this.state.data.oldPrice ? style1 : style2}
                >
                  {this.state.data.price}

                  <span>€</span>
                </h5>
              </div>
              <div id="buyAndFavoriteBTNs">
                <div id="kaufenBTN" onClick={_ =>addToBaskt(this.state.data.id)}>
                  <svg
                    id="kaufenSVG"
                    data-name="Layer 1"
                    viewBox="0 0 24 24"
                    width="20px"
                    height="20px"
                  >
                    <path d="M20 20a2 2 0 11-2-2 2 2 0 012 2zM8 18a2 2 0 102 2 2 2 0 00-2-2zM21.5 4H5.72l-.56-1a2 2 0 00-1.74-1H2.5a.5.5 0 00-.5.5v1a.5.5 0 00.5.5h.92l4.28 7.49a2.19 2.19 0 00.27.35l-1.86 3.7a.77.77 0 000 .71l.22.38A.76.76 0 007 17h12.5a.5.5 0 00.5-.5v-1a.5.5 0 00-.5-.5H8.62l1-2.09a2.74 2.74 0 00.64.09h6.23A2.94 2.94 0 0019 11.49l2.35-4.13A4.76 4.76 0 0022 5v-.5a.5.5 0 00-.5-.5z"></path>
                  </svg>
                  <h4 id="KaufenText">In den Warenkorb</h4>
                </div>
                <div id="addToFavoriteBtn" onClick={_ =>addTofavorit(this.state.data.id)}>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    height="24"
                    viewBox="0 0 24 24"
                    width="24"
                    id="FavoriteSVG"
                  >
                    <path id="fullHeart" fill="none" d="M12 21.35l-1.45-1.32C5.4 15.36 2 12.28 2 8.5 2 5.42 4.42 3 7.5 3c1.74 0 3.41.81 4.5 2.09C13.09 3.81 14.76 3 16.5 3 19.58 3 22 5.42 22 8.5c0 3.78-3.4 6.86-8.55 11.54L12 21.35z"/>
                    <path id="strokeHeart" d="M16.5 3c-1.74 0-3.41.81-4.5 2.09C10.91 3.81 9.24 3 7.5 3 4.42 3 2 5.42 2 8.5c0 3.78 3.4 6.86 8.55 11.54L12 21.35l1.45-1.32C18.6 15.36 22 12.28 22 8.5 22 5.42 19.58 3 16.5 3zm-4.4 15.55l-.1.1-.1-.1C7.14 14.24 4 11.39 4 8.5 4 6.5 5.5 5 7.5 5c1.54 0 3.04.99 3.57 2.36h1.87C13.46 5.99 14.96 5 16.5 5c2 0 3.5 1.5 3.5 3.5 0 2.89-3.14 5.74-7.9 10.05z" />
                  </svg>
                  <h4 id="FavoritBtnText">Zum Merkzettel hinzufügen</h4>
                </div>
              </div>
            </div>
          </div>
          <ProductWindows
            title="Sugesigtion"
            id="product1"
            width="fullWidthProductWindows"
            scale={0.6}
            productsData={ProductData}
          />
          <ProductWindows
            title="das kann dir auch gefalen"
            id="product"
            width="fullWidthProductWindows"
            scale={0.6}
            productsData={ProductData}
          />
        </div>
      );
    }
  }
}

function addTofavorit(id) {
  console.log("the Article with the ID :",id,"is sendt to favorite")
  document.getElementById("fullHeart").style.fill="red"
  document.getElementById("strokeHeart").style.fill="none"
}
function addToBaskt(id) {
  console.log("the Article with the ID :",id,"is sendt to basket")
}
export default ProductPage;
