import React from "react";
import Navbar from "./NavBar";
import { Ads } from "./ads";
import Slider from "./slider";
import ProductWindows from "./ProductWindows";
import { ProductData } from "./product/ProductData";
import DesktopMenu from "./DesktopMenu";
import { Sortiment } from "./Sortiment";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useParams,

  // useRouteMatch
} from "react-router-dom";
import ProductPage from "./product/ProductPage";
const picsData = [
  
  "https://source.unsplash.com/600x400/?coffee",
  "https://source.unsplash.com/600x400/?coffee,coffee-beans",
  "https://source.unsplash.com/600x400/?coffee",
 
];

function App() {
  return (
    <Router>
      <Navbar />
     
      <Switch>
        <Route exact path="/">
        <Ads />
          <div id="HeaderView">
            <Slider data={picsData} />
          </div>

          <ProductWindows
            title="Top seller"
            id="fullwidth"
            width="fullWidthProductWindows"
            scale={0.7}
            productsData={ProductData}
          />

          <div id="BodyContainer">
            <div id="asideContainer">
              <div id="DesktopMenuContainer">
                <h4 id="DesktopMenuTitel">Unser Sortiment</h4>
                <ul id="DesktopMenuList">
                  {Sortiment.map((e, i) => (
                    <DesktopMenu
                      key={i}
                      title={e.title}
                      id={e.id}
                      SubSortiment={e.SubSortiment}
                    />
                  ))}
                </ul>
              </div>
            </div>

            <div id="MainContainer">
              <ProductWindows
                title="Top seller"
                id="e3"
                scale={0.7}
                productsData={ProductData}
              />
              <ProductWindows
                title="Sugesigtion"
                id="e1"
                scale={0.6}
                productsData={ProductData}
              />
              <ProductWindows
                title="last seen"
                id="e2"
                scale={0.6}
                productsData={ProductData}
              />
            </div>
          </div>
        </Route>
        <Route path="/ProductsPage/:id" strict children={<ChildProducts />} />
      </Switch>
      <footer></footer>
    </Router>
   
  );
}

function ChildProducts() {
  let { id } = useParams();
  return <ProductPage id={id} />;
  
}


export default App;
