function MenuSubMenu(props) {
  return (
    <Fragment>
      <li
        className="SubDesktopMenuListItem"
        onClick={_ => console.log(props.link)}
      >
        {props.name}
      </li>
    </Fragment>
  );
}
class DesktopMenu extends Component {
  toggleList = async _ => {
    const el = document.getElementById(this.props.id + "ul1");
    const svg = document.getElementById(this.props.id + "svg1");
    const getElStyle = getComputedStyle(el).getPropertyValue("display");
    el.style.display = "block";
    const getElHeight = getComputedStyle(el).getPropertyValue("height");
    getElStyle === "none"
      ? (el.style.display = "none")
      : (el.style.display = "block");
    if (getElStyle === "none") {
      el.style.display = "block";
      el.style.height = 0;
      await wait(1);
      el.style.height = getElHeight;
      el.style.transform = "scaleY(1)";
      svg.style.transform = "rotate(0)";
    } else {
      el.style.height = "0px";
      el.style.transform = "scaleY(0)";
      svg.style.transform = "rotate(-90deg)";
      await wait(350);
      el.style.display = "none";
      el.style.height = "auto";
    }
  };
  render() {
    return (
      <Fragment>
        <li className="DesktopMenuListItem">
          <div
            style={{
              width: "100%",
              height: "40px",
              position: "absolute",
              top: 0,
              left: 0
            }}
            onClick={this.toggleList}
          ></div>
          <svg
            className="collaps"
            id={this.props.id + "svg1"}
            xmlns="http://www.w3.org/2000/svg"
            height="24"
            viewBox="0 0 24 24"
            width="24"
            onClick={this.toggleList}
          >
            <path d="M0 0h24v24H0V0z" fill="none" />
            <path d="M7.41 8.59L12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z" />
          </svg>
          {this.props.title}
          <ul className="MenuSubMenu" id={this.props.id + "ul1"}>
            {this.props.SubSortiment["name"].map((el, index) => (
              <MenuSubMenu
                key={index}
                name={el}
                link={
                  this.props.SubSortiment["link"][
                    this.props.SubSortiment["name"].indexOf(el)
                  ]
                }
              />
            ))}
          </ul>
        </li>
      </Fragment>
    );
  }
}
export default DesktopMenu;
