import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import Rating from "./Rating";
import { wait } from "../slider";

const style1 = {
  color: "red",
  marginLeft: "10px"
};
const style2 = {
  color: "black",
  marginLeft: "0"
};

const actionStyle = {
  backgroundColor: "red"
};
class Product extends Component {
  state = { key: null };

  render() {
    // if (this.state.redirect) {
    //   return <Redirect to={this.state.redirect} />
    // }
    return (
      <Link to={`/productsPage/${this.props.id}`} style={{ color: "black" }} >
            
        <div
          className="productContainer"
          id="productContainerid"
           onClick={async _=> {
             console.log("sdsdsd")
         await wait(10)
         window.location.reload()

           }}
       
          style={{
            transform: "scale(" + this.props.scale + ")",
            marginRight: this.props.scale
              ? (this.props.scale * 285 - 285) / 2 + 5 + "px"
              : "15px",
            marginLeft: this.props.scale
              ? (this.props.scale * 285 - 285) / 2 + 5 + "px"
              : "15px",
            marginTop: this.props.scale
              ? (this.props.scale * 400 - 400) / 2 + 5 + "px"
              : "15px",
            marginBottom: this.props.scale
              ? (this.props.scale * 400 - 400) / 2 + 5 + "px"
              : "15px"
          }}
        >
          <div
            className="action"
            style={this.props.action ? actionStyle : null}
          >
            <h4>{this.props.action}</h4>
          </div>

          <div
            className="productPic"
            style={{
              backgroundImage: `url(${this.props.img})`
            }}
          >
            {/* <div
            className="action"
            //   style={{ backgroundImage: URL(this.props.actionPic) }}
          > */}
          </div>

          <div id="productInfo">
            <Rating rate={this.props.rate} />
            <h5 id="productName" title={this.props.name}>
              {this.props.name}
            </h5>
            <h5 id="productNote">{this.props.note}</h5>
            <div className="ProductPricing">
              <h5 id="productOldPrice">
                {this.props.oldPrice}
                {this.props.oldPrice ? "€" : ""}
              </h5>
              <h5
                id="productPrice"
                style={this.props.oldPrice ? style1 : style2}
              >
                {this.props.price}

                <span>€</span>
              </h5>
            </div>
          </div>
        </div>
      </Link>
    );
  }
}
export default Product;
