import React, { Component } from "react";
import Product from "./product/product";
import { isMobile } from "./isMobile";
import { wait } from "./slider";


class ProductWindows extends Component {
  componentDidMount() {
    // document.getElementById("fullwidth").style.width =
    //   "100%";
    if (isMobile()) {
      const el = document.getElementsByClassName("EmpfehlungenContainer");
      const elArrow = document.getElementsByClassName("productArrow");
      const titleEl = document.getElementsByClassName(
        "EmpfehlungenContainerTitle"
      );
      // show scroll bar on mobile
      for (let index = 0; index < el.length; index++) {
        el[index].style.overflowX = "auto";
      }
      // hide arrows on mobile
      for (let index = 0; index < elArrow.length; index++) {
        elArrow[index].style.display = "none";
      }
      // center the products catagories text on mobile
      for (let index = 0; index < titleEl.length; index++) {
        titleEl[index].style.textAlign = "center";
      }
    }
  }
  // Right arrow onClick function
  scrollToRight = async _ => {
    const el = document.getElementById(this.props.id);
    const getElWidth = Number(getComputedStyle(el).getPropertyValue("width").split("px")[0])
    // scroll position start from the left
    el.scrollTo({
      // the scroll position from the left + the element container width
      left: el.scrollLeft + getElWidth,
      behavior: "smooth"
    });
    // hide the arrows while scrolling
    document.getElementById("productRightArrow" + this.props.id).style.display =
      "none";
    document.getElementById("productLeftArrow" + this.props.id).style.display =
      "none";
    // wait until the scroll is done, and refresh the new scroll position
    await wait(900);
    // repostion the left and the right arrows
    document.getElementById("productRightArrow" + this.props.id).style.right =
      -el.scrollLeft + "px";
    document.getElementById("productLeftArrow" + this.props.id).style.left =
      el.scrollLeft + "px";
    // reshow the arrows after the scrolling is done
    document.getElementById("productRightArrow" + this.props.id).style.display =
      "inline-flex";
    document.getElementById("productLeftArrow" + this.props.id).style.display =
      "inline-flex";
    
  };
  // left arrow onClick function
  scrollToLeft = async _ => {
    const el = document.getElementById(this.props.id);
    const getElWidth = Number(getComputedStyle(el).getPropertyValue("width").split("px")[0])
    el.scrollTo({
      // prevent scroll bellow 0
      left: el.scrollLeft <= 0 ? 0 : el.scrollLeft - getElWidth,
      behavior: "smooth"
      
    });
    
    // hide the arrows while scrolling
    document.getElementById("productRightArrow" + this.props.id).style.display =
      "none";
    document.getElementById("productLeftArrow" + this.props.id).style.display =
      "none";
    // wait until the scroll is done, and refresh the new scroll position
    await wait(900);
    // repostion the left and the right arrows
    document.getElementById("productRightArrow" + this.props.id).style.right =
      -el.scrollLeft + "px";
    document.getElementById("productLeftArrow" + this.props.id).style.left =
      el.scrollLeft + "px";
    // reshow the arrows after the scrolling is done
    document.getElementById("productRightArrow" + this.props.id).style.display =
      "inline-flex";
    document.getElementById("productLeftArrow" + this.props.id).style.display =
      "inline-flex";
  };
  render() {
    return (
      <div className="windows">
        <h5 className="EmpfehlungenContainerTitle">{this.props.title}</h5>
        <div
          className={`EmpfehlungenContainer ${this.props.width}` }
          id={this.props.id}
          style={{ height: this.props.scale?( this.props.scale * 400) + 20 + "px" : "" }}
        >
          <svg
            onClick={this.scrollToLeft}
            className="productArrow productLeftArrow"
            id={"productLeftArrow" + this.props.id}
            xmlns="http://www.w3.org/2000/svg"
            height="50"
            viewBox="0 0 24 24"
            width="50"
          >
            <path d="M15.41 16.59L10.83 12l4.58-4.59L14 6l-6 6 6 6 1.41-1.41z" />
          </svg>
          {this.props.productsData.map((el, index) => (
            <Product
              id={el.id}
              key={index}
              action={el.action}
              img={el.img}
              pic={el.pic}
              name={el.name}
              price={el.price}
              rate={el.rating}
              note={el.note}
              oldPrice={el.oldPrice}
              scale={this.props.scale}
            />
          ))}
          <svg
            onClick={this.scrollToRight}
            className="productArrow productRightArrow"
            id={"productRightArrow" + this.props.id}
            xmlns="http://www.w3.org/2000/svg"
            height="50"
            viewBox="0 0 24 24"
            width="50"
          >
            <path d="M8.59 16.59L13.17 12 8.59 7.41 10 6l6 6-6 6-1.41-1.41z" />
          </svg>
        </div>
      </div>
    );
  }
}

export default ProductWindows;
